import React from 'react';
import Grow from '@material-ui/core/Grow';
import TextField from '@material-ui/core/TextField';
import SearchIcon from '@material-ui/icons/Search';
import IconButton from '@material-ui/core/IconButton';
import ClearIcon from '@material-ui/icons/Clear';
import { withStyles } from '@material-ui/core/styles';

const defaultSearchStyles = theme => ({
  main: {
    display: 'flex',
    flex: '1 0 auto',
  },
  searchIcon: {
    color: theme.palette.text.secondary,
    marginTop: '10px',
    marginRight: '8px',
  },
  searchText: {
    flex: '1 0',
    minWidth: 160,
    '& input': {
      fontSize: 14,
    },
  },
  clearIcon: {
    '&:hover': {
      color: theme.palette.error.main,
    },
  },
});

class TableSearch extends React.Component {
  handleTextChange = event => {
    const { onSearchChange, performSearchOnEnterKey } = this.props.options;
    if (!performSearchOnEnterKey) {
      if (onSearchChange) {
        onSearchChange(event.target.value);
      }
      this.props.onSearch(event.target.value);
    }
  };

  componentDidMount() {
    document.addEventListener('keydown', this.onKeyDown, false);
  }

  componentWillUnmount() {
    document.removeEventListener('keydown', this.onKeyDown, false);
  }

  onKeyDown = event => {
    const { performSearchOnEnterKey } = this.props.options;
    if (event.keyCode === 27) {
      this.props.onHide();
    }
    if (event.keyCode === 13 && performSearchOnEnterKey) {
      this.props.onSearch(event.target.value);
    }
  };

  render() {
    const { classes, options, onHide } = this.props;

    return (
      <Grow appear in={true} timeout={300}>
        <div className={classes.main} ref={el => (this.rootRef = el)}>
          <SearchIcon className={classes.searchIcon} />
          <TextField
            className={classes.searchText}
            placeholder={this.props.options.searchInputPlaceholder ? this.props.options.searchInputPlaceholder : ''}
            autoFocus={
              this.props.options.searchWithFocusIfVisible === undefined
                ? true
                : this.props.options.searchWithFocusIfVisible
            }
            InputProps={{
              'aria-label': options.textLabels.toolbar.search,
            }}
            onChange={this.handleTextChange}
            fullWidth={true}
            inputRef={el => (this.searchField = el)}
          />
          <IconButton className={classes.clearIcon} onClick={onHide}>
            <ClearIcon />
          </IconButton>
        </div>
      </Grow>
    );
  }
}

export default withStyles(defaultSearchStyles, { name: 'MUIDataTableSearch' })(TableSearch);
